import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, Events } from 'ionic-angular';
import { FormBuilder, FormControl, FormGroup, Validators } from '@angular/forms';
import { AuthProvider } from '../../providers/auth/auth';
import { UtiltiyProvider } from '../../providers/utiltiy/utiltiy';
import { PrestartPage } from '../prestart/prestart';
import { HomePage } from '../home/home';
import { CabsandPrestartPage } from '../cabsand-prestart/cabsand-prestart';
import { KlmrNewsPage } from '../klmr-news/klmr-news';
import { CabsandNewsPage } from '../cabsand-news/cabsand-news';
@IonicPage()
@Component({
  selector: 'page-login',
  templateUrl: 'login.html',
})
export class LoginPage {
  credentialsForm: FormGroup;
  companyName:any = "";
  selectOptions: any;
  constructor(public navCtrl: NavController, public navParams: NavParams, private formBuilder: FormBuilder, public auth: AuthProvider, public events: Events, public utiltiy: UtiltiyProvider) {
   this.getCompanyName();
   this.navCtrl.swipeBackEnabled = false;
    this.credentialsForm = this.formBuilder.group({
      username: new FormControl('', [Validators.required]),
      password: new FormControl('', [Validators.required]),
    });
    this.selectOptions = {
      mode: 'ios'
    };
  }

  async getCompanyName(){
    let currentGroup = await this.utiltiy.getStorage("currentClass");
    console.log("currentGroup",currentGroup);
    if(currentGroup){
      
      this.companyName = currentGroup;
      this.events.publish('currentClass',currentGroup);
    }
  }



  login() {
    console.log("this.credentialsForm.value", this.credentialsForm.value)
    let formData: FormData = new FormData();
    formData.append('username', this.credentialsForm.value.username);
    formData.append('password', this.credentialsForm.value.password);
    this.utiltiy.startLoading();
    this.auth.login(formData)
      .then((result: any) => {
        console.log("result", result)
        this.utiltiy.stopLoading();

        if (result.data) {
          this.utiltiy.toaster(result.message);
          this.events.publish('chnageSideMenu', result.data.roleID);
          this.utiltiy.setStorage("token", result.token);
          this.utiltiy.setStorage("roleID", result.data.roleID);
          this.utiltiy.setStorage("roleName", result.data.roleName);
          this.utiltiy.setStorage("name", result.data.firstName+" "+result.data.surname);
          this.utiltiy.setStorage("permissions", JSON.stringify(result.data.permissions));
          this.utiltiy.setStorage("primaryColor", "#b71464");
          this.goTo();
        }
      }, err => {
        console.log("err", err.error.message)
        this.utiltiy.stopLoading();
        this.utiltiy.toaster(err.error.message);
      })
  }

  async goTo() {
    let currentGroup = await this.utiltiy.getStorage("currentClass");
    if (currentGroup == "klmr") {
      this.navCtrl.setRoot(KlmrNewsPage);
    } else if(currentGroup == "cabsand"){
      this.navCtrl.setRoot(CabsandNewsPage);
    }
  }

  selectType(type){
    console.log("typw",this.companyName);
    this.utiltiy.setStorage("currentClass",type);
    this.events.publish('currentClass',type);

  }
}



