import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { CrudProvider } from '../../providers/crud/crud';
import { UtiltiyProvider } from '../../providers/utiltiy/utiltiy';
import { FormBuilder, FormControl, FormGroup, Validators } from '@angular/forms';
import { CabsandFuelPage } from '../cabsand-fuel/cabsand-fuel';
@IonicPage()
@Component({
  selector: 'page-cabsand-fuel-add',
  templateUrl: 'cabsand-fuel-add.html',
})
export class CabsandFuelAddPage {
  fleetList: any = [];
  filledByList: any = [];
  containerList: any = [
    // "Truck",
    // "Gerry Cans",
    "Pod",
  ];
  fuelList: any = [
    "Diesel",
    // "Unleaded",
  ];
  serviceStationList:any = [
    "Service 1",
    "Service 2",
    "Service 3",
    "Service 4",
    "Service 5",
  ];
  serviceStationName:any = "";
  selectedFleet: any;
  selectedFuel: any;
  selectedConatiner: any;
  filledBy: any;
  selectOptions: any;
  createFuel: FormGroup;
  isUpdate: boolean = false;
  params: any = {};
  appColor: String;
  constructor(public navCtrl: NavController, public navParams: NavParams, private api: CrudProvider, public utiltiy: UtiltiyProvider, private formBuilder: FormBuilder) {
    this.selectOptions = {
      mode: 'ios'
    };
    this.params = this.navParams.get('param');
    if (this.params) {
      this.isUpdate = true;

    }
    let currentDate = new Date()
    let date = currentDate.toISOString().split('T')[0]
    this.createFuel = this.formBuilder.group({
      filled_by: new FormControl('', [Validators.required]),
      date: new FormControl(date, [Validators.required]),
      odometer: new FormControl('', [Validators.required]),
      diesel: new FormControl('Diesel', [Validators.required]),
      container: new FormControl('Pod', [Validators.required]),
      fleet_no: new FormControl('', [Validators.required]),
      fuel: new FormControl('', [Validators.required]),
      amount: new FormControl('', [Validators.required]),
      fuel_odr: new FormControl('', [Validators.required]),
      location: new FormControl('', [Validators.required]),
      fuel_station: new FormControl('Service 1', [Validators.required]),
    });
    console.log("param", this.params)
  }

  ionViewDidLoad() {
    this.getappColor();
    this.getMetaData();

  }

  async getappColor(){
    this.appColor = await this.utiltiy.getStorage("primaryColor");
    let userName = await this.utiltiy.getStorage("name");
    if(userName){
      this.createFuel.get(`filled_by`).setValue(userName);
      this.filledBy = userName;
    }
  }

  async getMetaData() {
    this.fleetList = await this.getFleetList();
    this.filledByList = await this.getFilledBtList();
    if (this.params) {
      this.regenerateForm();
    }
  }

  async getFleetList() {
    this.fleetList = [];
    this.utiltiy.startLoading();
    return this.api.get("/tansix_prestart/fleet/get", false)
      .then((result: any) => {
        console.log("result", result)
        this.utiltiy.stopLoading();
        if (result.status) {
          return result.result;
        }
      }, err => {
        this.utiltiy.stopLoading();
        this.utiltiy.toaster(err.error.message);
        return [];
      })
  }
  async getFilledBtList() {
    this.filledByList = [];
    this.utiltiy.startLoading();
    return this.api.get("/tansix_prestart/fuel/users/get", false)
      .then((result: any) => {
        console.log("result", result)
        result.result.forEach(element => {
          element.name = element.firstName+" "+element.surname;
        });
        this.utiltiy.stopLoading();
        if (result.status) {
          return result.result;
        }
      }, err => {
        this.utiltiy.stopLoading();
        this.utiltiy.toaster(err.error.message);
        return [];
      })
  }

  regenerateForm() {
    this.createFuel.patchValue({
      filled_by: this.params.filled_by,
      date: this.params.date,
      odometer: this.params.odometer,
      diesel: this.params.diesel,
      container: this.params.container,
      fleet_no: this.params.fleet_no,
      fuel: this.params.fuel,
      fuel_odr: this.params.fuel_odr,
      amount: this.params.amount,
      location: this.params.location,
      fuel_station: this.params.fuel_station,
    });
    this.serviceStationName = this.params.fuel_station;
    this.filledBy = this.params.filled_by;
    this.selectedConatiner = this.params.container;
    this.selectedFleet = this.params.fleet_no;
    this.selectedFuel = this.params.diesel;
  }

  dropDownChange(value, key) {
    this.createFuel.get(`${key}`).setValue(value);
    if (key == "diesel" || key == "container") {
      this.fuelCalculator();
    }
  }

  fuelCalculator() {
    if (this.createFuel.value.container.length != 0 && this.createFuel.value.diesel.length != 0 && this.createFuel.value.fuel.length != 0) {
      this.fetchAmount();
    }
  }

  fetchAmount() {
    this.createFuel.get('amount').setValue("");
    // this.utiltiy.startLoading();
    let formData: FormData = new FormData();
    formData.append("container",this.createFuel.value.container);
    formData.append("diesel",this.createFuel.value.diesel);
    formData.append("fuel",this.createFuel.value.fuel);
    this.api.post(`/tansix_prestart/fuel/fuel/detail`, false, formData)
      .then((result: any) => {
        // this.utiltiy.stopLoading();
        if (result.status) {
          if(result.result.success){
            this.createFuel.get('amount').setValue(result.result.price);
          }else{
            this.utiltiy.toaster(result.result.error);
          }
        }

      }, err => {
        console.log("err", err)
        // this.utiltiy.stopLoading();
        this.utiltiy.toaster(err.error.message);
      })
  }

  submit() {
    console.log("this.credentialsForm.value", this.createFuel.value)
    let formKeys = Object.keys(this.createFuel.value);
    if (this.isUpdate) {
      let string = "";
      formKeys.forEach(key => {
        if(string.length == 0){
          string = string+""+key+"="+this.createFuel.value[key];
        }else{
          string = string+"&"+key+"="+this.createFuel.value[key];
        }
      });
      let body = string;
      console.log("string-1",body);
      this.updateFuel(body, this.params.id);
    } else {
      let formData: FormData = new FormData();
      formKeys.forEach(key => {
        formData.append(key, this.createFuel.value[key]);
      });
      this.addFuel(formData);
    }



  }

  addFuel(formData) {
    this.api.post(`/tansix_prestart/fuel/fuel/store`, false, formData)
      .then((result: any) => {
        console.log("result", result)
        this.utiltiy.stopLoading();
        if (result.status) {
          this.utiltiy.toaster(result.message);
          this.navCtrl.setRoot(CabsandFuelPage);
        }

      }, err => {
        console.log("err", err)
        this.utiltiy.stopLoading();
        this.utiltiy.toaster(err.error.message);
      })
  }

  updateFuel(formData, id) {
    this.api.put(`/tansix_prestart/fuel/fuel/update/${id}`, false, formData)
      .then((result: any) => {
        console.log("result", result)
        this.utiltiy.stopLoading();
        if (result.status) {
          this.utiltiy.toaster(result.message);
          this.navCtrl.setRoot(CabsandFuelPage);
        }

      }, err => {
        console.log("err", err)
        this.utiltiy.stopLoading();
        this.utiltiy.toaster(err.error.message);
      })
  }

  

}



