import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { CrudProvider } from '../../providers/crud/crud';
import { UtiltiyProvider } from '../../providers/utiltiy/utiltiy';
import { KlmrFuelTruckAddPage } from '../klmr-fuel-truck-add/klmr-fuel-truck-add';

@IonicPage()
@Component({
  selector: 'page-klmr-fuel-truck',
  templateUrl: 'klmr-fuel-truck.html',
})
export class KlmrFuelTruckPage {
  fuelTruckList: any = [];
  constructor(public navCtrl: NavController, public navParams: NavParams, private api: CrudProvider, public utiltiy: UtiltiyProvider) {
  }

  ionViewDidLoad() {
    this.getfuelTruckList();
  }



  getfuelTruckList() {
    this.fuelTruckList = [];
    this.utiltiy.startLoading();
    this.api.get("/prestart/fuel/fuel_truck/get", false)
      .then((result: any) => {
        console.log("result", result)
        this.fuelTruckList = result.result;
        this.utiltiy.stopLoading();
      }, err => {
        this.utiltiy.stopLoading();
        this.utiltiy.toaster(err.error.message);
      })
  }


  async deleteRecord(id) {
    let status = await this.utiltiy.showDialog();
    if (status) {
      this.deleteTruckList(id);
    }
    console.log("status", status);
  }


  deleteTruckList(id) {
    this.api.delete(`/prestart/fuel/fuel_truck/delete/${id}`, false)
      .then((result: any) => {
        console.log("result", result)
        this.utiltiy.stopLoading();
        if (result.status) {
          this.utiltiy.toaster(result.message);
          this.getfuelTruckList();
        }

      }, err => {
        this.utiltiy.stopLoading();
        this.utiltiy.toaster(err.error.message);
      })
  }
  
  addFuelTruck(){
   this.navCtrl.push(KlmrFuelTruckAddPage, { param: null });
 }
 updateFuelTruck(prestart){
   this.navCtrl.push(KlmrFuelTruckAddPage, { param: prestart });
 }

}



