import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { CrudProvider } from '../../providers/crud/crud';
import { UtiltiyProvider } from '../../providers/utiltiy/utiltiy';
import { KlmrJsaAddPage } from '../klmr-jsa-add/klmr-jsa-add';

@IonicPage()
@Component({
  selector: 'page-klmr-jsa',
  templateUrl: 'klmr-jsa.html',
})
export class KlmrJsaPage {
  jsaList: any = [];
  constructor(public navCtrl: NavController, public navParams: NavParams, private api: CrudProvider, public utiltiy: UtiltiyProvider) {
  }

  ionViewDidLoad() {
    this.getJlaList();
  }

 

  

  getJlaList() {
    this.jsaList = [];
    this.utiltiy.startLoading();
    this.api.get("/prestart/jsa/get", false)
      .then((result: any) => {
        console.log("result", result)
        this.jsaList = result.result;
        this.utiltiy.stopLoading();
      }, err => {
        this.utiltiy.stopLoading();
        this.utiltiy.toaster(err.error.message);
      })
  }


  async deleteRecord(id) {
    let status = await this.utiltiy.showDialog();
    if (status) {
      this.deleteJsa(id);
    }
    console.log("status", status);
  }


  deleteJsa(id) {
    this.api.delete(`/prestart/jsa/delete/${id}`, false)
      .then((result: any) => {
        console.log("result", result)
        this.utiltiy.stopLoading();
        if (result.status) {
          this.utiltiy.toaster(result.message);
          this.getJlaList();
        }

      }, err => {
        this.utiltiy.stopLoading();
        this.utiltiy.toaster(err.error.message);
      })
  }
  
  addjsa(){
   this.navCtrl.push(KlmrJsaAddPage, { param: null });
 }
 updateJsa(payload){
   this.navCtrl.push(KlmrJsaAddPage, { param: payload });
 }

}




